import {Component, ChangeDetectionStrategy, ViewChild, TemplateRef, OnInit} from '@angular/core';
import {map, filter, catchError, mergeMap, tap} from 'rxjs/operators';

import {
    isSameDay,
    isSameMonth,
} from 'date-fns';

import {Observable, Subject} from 'rxjs';
import {
    CalendarEvent,
    CalendarEventAction,
    CalendarEventTimesChangedEvent, CalendarEventTitleFormatter,
    CalendarView
} from 'angular-calendar';
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
import {environment} from "../../../environments/environment";
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import {ModalDismissReasons, NgbModal, NgbTimepickerConfig, NgbTimeStruct} from "ar-bootstrap";

import {CustomEventTitleFormatter} from './custom-event-title-formatter.provider';
import {AuthService} from "../../auth/services/auth.service";

interface EventCalendar {
    id: number,
    title: string,
    description: string,
    start: string,
    end: string,
    color: string,
    all_day: boolean
}

const colors: any = {
    red: {
        primary: '#ad2121',
        secondary: '#FAE3E3'
    },
    blue: {
        primary: '#1e90ff',
        secondary: '#D1E8FF'
    },
    yellow: {
        primary: '#e3bc08',
        secondary: '#FDF1BA'
    }
};

@Component({
    selector: 'app-home',
    changeDetection: ChangeDetectionStrategy.OnPush,
    templateUrl: './list.component.html',
    styleUrls: ['./list.component.css'],
    providers: [
        {
            provide: CalendarEventTitleFormatter,
            useClass: CustomEventTitleFormatter
        }
    ]
})
export class ListComponent implements OnInit {

    @ViewChild('modalContent') modalContent: TemplateRef<any>;

    @ViewChild('modalDelete') modalDelete: TemplateRef<any>;

    view: CalendarView = CalendarView.Month;

    CalendarView = CalendarView;

    viewDate: Date = new Date();

    modalData: {
        action: string;
        event: CalendarEvent;
    };

    refresh: Subject<any> = new Subject();

    actions: CalendarEventAction[] = [
        {
            label: '<i class="fa fa-fw fa-pencil"></i>',
            onClick: ({event}: { event: CalendarEvent }): void => {
                this.handleEvent('Edited', event);
            }
        },
        {
            label: '<i class="fa fa-fw fa-times"></i>',
            onClick: ({event}: { event: CalendarEvent }): void => {
                //this.events$ = this.events$.pipe(filter((iEvent, index) => iEvent[index] !== event));
                //this.handleEvent('Deleted', event);
                this.modalData = {event: event, action: "Deleted"};
                this.modal.open(this.modalDelete);
            }
        }
    ];

    events$: Observable<Array<CalendarEvent<{ event: EventCalendar }>>>;

    activeDayIsOpen: boolean = true;

    f: FormGroup;
    errorCredentials = false;
    timeStart: NgbTimeStruct = {hour: null, minute: null, second: null};
    timeEnd: NgbTimeStruct = {hour: null, minute: null, second: null};
    allDay: boolean = false;

    closeResult: string;

    constructor(private modal: NgbModal, private formBuilder: FormBuilder, private http: HttpClient, config: NgbTimepickerConfig) {
        config.seconds = false;
        config.spinners = false;
    }

    fetchEvents(): void {
        this.events$ = this.http
            .get(`${environment.api_url}/events`)
            .pipe(
                map(({events}: { events: EventCalendar[] }) => {
                    return events.map((event: EventCalendar) => {
                        return {
                            start: new Date(event.start),
                            end: new Date(event.end),
                            title: event.title,
                            color: colors[event.color],
                            allDay: !!(event.all_day),
                            actions: this.actions,
                            resizable: {
                                beforeStart: true,
                                afterEnd: true
                            },
                            draggable: true,
                            meta: {
                                event
                            }
                        };
                    });
                })
            );
    }

    dayClicked({date, events}: { date: Date; events: Array<CalendarEvent<{ event: EventCalendar }>> }): void {
        if (isSameMonth(date, this.viewDate)) {
            this.activeDayIsOpen = !((isSameDay(this.viewDate, date) && this.activeDayIsOpen === true) ||
                events.length === 0);
            this.viewDate = date;
        }
    }

    eventTimesChanged({event, newStart, newEnd}: CalendarEventTimesChangedEvent): void {
        this.events$ = this.events$.pipe(map(events => {
            return events.map(iEvent => {
                if (iEvent.meta.event.id == event.meta.event.id) {
                    return {
                        ...event,
                        start: newStart,
                        end: newEnd
                    };
                } else {
                    return iEvent;
                }
            })
        }));
        this.handleEvent('Dropped or resized', event);
    }

    handleEvent(action: string, event: CalendarEvent): void {
        this.modalData = {event, action};
        this.modal.open(this.modalContent, {size: 'lg'});
    }

    addEvent(): void {
        // this.events$ = [
        //   ...this.events$,
        //   {
        //     title: 'New event',
        //     start: startOfDay(new Date()),
        //     end: endOfDay(new Date()),
        //     color: {
        //       primary: '#e3bc08',
        //           secondary: '#FDF1BA'
        //     },
        //     draggable: true,
        //     resizable: {
        //       beforeStart: true,
        //       afterEnd: true
        //     }
        //   }
        // ];
    }

    deleteEvent(eventToDelete: CalendarEvent) {
        this.modal.dismissAll()
        this.http.delete<any>(`${environment.api_url}/events/${eventToDelete.meta.event.id}`)
            .toPromise()
            .then(
                (data) => {
                    console.log(data)
                    if (data.status == 'success') {
                        this.refresh.next();
                    } else {
                        console.log('error')
                    }
                    this.refresh.next();
                    this.fetchEvents();
                },
                (errorResponse: HttpErrorResponse) => {
                    if (errorResponse.status == 400 || errorResponse.status == 500) {
                        alert('error')
                    }
                    this.refresh.next();
                },
            );
    }

    setView(view: CalendarView) {
        this.view = view;
    }

    closeOpenMonthViewDay() {
        this.activeDayIsOpen = false;
    }

    ngOnInit() {
        this.fetchEvents();

        this.f = this.formBuilder.group({
            title: [null, [Validators.required]],
            description: [null, [Validators.nullValidator]],
            dateStart: [null, [Validators.required]],
            timeStart: [null, [Validators.required]],
            dateEnd: [null, [Validators.required]],
            timeEnd: [null, [Validators.required]],
            color: [null, [Validators.required]]
        })
    }

    open(content) {
        this.modal.open(content, {ariaLabelledBy: 'modal-basic-title'}).result.then((result) => {
            this.closeResult = `Closed with: ${result}`;
            this.onSubmit();
        }, null);
    }

    onSubmit() {
        this.http.post<any>(`${environment.api_url}/events`, this.f.value)
            .subscribe(
                response => {
                    this.refresh.next();
                    this.fetchEvents();
                },
                (errorResponse: HttpErrorResponse) => {
                    console.log(errorResponse);
                });
    }
}
